@mixin flex {
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact {
    @include flex;
    margin-top: 8%;
    padding-top: 2%;
    padding-bottom: 6%;
    justify-content: space-around;
    width: 100%;
    height: 600px;
    background: rgb(29, 29, 29); 
    img {
        width: 100px;
        height: 100px;
    }
    div {
        @include flex;
        flex-direction: column;
        color: white;

        h1 {
            font-size: 16px;
            color: rgb(104, 104, 104);
            margin-bottom: 1%
        }
    }
    .footer-menu {
        div {
            @include flex;
            justify-content: initial;
            a {
                text-decoration: none;
                color: white;
            }
        }
      
    }
}